.container {
    text-align: center;
    display: inline-block;
    @media only screen and (max-width: 450px) {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 100vw;
    }
}
.buttondark {
    font-size: 1.5vw;
    text-decoration: none;
    display: inline-block;
    font-family: "Montserrat", sans-serif;
    padding-top: 0.4rem;
    padding-bottom: 0.4rem;
    width: 200px;
    text-align: center;
    border-radius: 0.5rem;
    border-width: 2px;
    color: rgb(233, 240, 243);
    transition: 500ms ease-in-out;
    @media only screen and (max-width: 1000px) {
        font-size: 2vw;
        width: 130px;
    }
    @media only screen and (max-width: 450px) {
        font-size: 6vw;
        width: 150px;
    }
}
.buttonlight:hover {
    color: white;
    background-color: black;
}

.buttonlight {
    font-size: 1.5vw;
    text-decoration: none;
    display: inline-block;
    font-family: "Montserrat", sans-serif;
    padding-top: 0.4rem;
    padding-bottom: 0.4rem;
    width: 200px;
    text-align: center;
    border-radius: 0.5rem;
    border-width: 2px;
    transition: 500ms ease-in-out;
    color: rgb(20, 19, 19);
    @media only screen and (max-width: 1000px) {
        font-size: 2vw;
        width: 130px;
    }
    @media only screen and (max-width: 450px) {
        font-size: 6vw;
        width: 150px;
    }
}
.buttondark:hover {
    color: rgb(20, 19, 19);
    background-color: rgb(233, 240, 243);
}
